import React from 'react';


class GetStatusOfTask extends React.Component{
  render(){
    const data = this.props.data;
    let status;
    switch(data){
      case 'Must be checked':
      status = <td className="red"></td>;
      break;
      case 'Checking':
      status = <td className="need-to-check"></td>;
      break;
      case 'In Progress':
      status = <td className="yellow"></td>;
      break;
      case 'ToDo':
      status = <td className="grey"></td>;
      break;
      case 'Checked by mentor':
      status = <td className="green"></td>;
      break;
      default:
      status = <td>Something went wrong</td>;
    } 
    return status;
  }
}

export default GetStatusOfTask;