import React from 'react';
import './GetStatusOfTask';
import GetStatusOfTask from './GetStatusOfTask';



class ListOfItems extends React.Component{
  render(){
    const data = this.props.data;
    const listOfItems = data.map((item, index) => {
      return (
          <tr key={item.studentGithub}>
            <td>{item.studentGithub}</td>
            <GetStatusOfTask data={item["Code Jam \"CV\""]}/>
            <GetStatusOfTask data={item["Code Jam \"CoreJS\""]}/>
            <GetStatusOfTask data={item["Code Jam \"DOM, DOM Events\""]}/>
            <GetStatusOfTask data={item["Markup #1"]}/>
            <GetStatusOfTask data={item["RS Activist"]}/>
            <GetStatusOfTask data={item["YouTube"]}/>
            <GetStatusOfTask data={item["CodeJam \"Scoreboard\""]}/>
            <GetStatusOfTask data={item["Game"]}/>
            <GetStatusOfTask data={item["Course work"]}/>
          </tr>
      )
    }
    );
    return (
      <div>
        <table>
          <thead>
            <tr key={111}>
              <th key={11}>Student Github</th>
              <th key={12}>{"Code Jam \"CV\""}</th> 
              <th key={13}>{"Code Jam \"CoreJS\""}</th>
              <th key={14}>{"Code Jam \"DOM, DOM Events\""}</th>
              <th key={15}>{"Markup #1"}</th>
              <th key={16}>{"RS Activist"}</th>
              <th key={17}>{"YouTube"}</th>
              <th key={18}>{"CodeJam \"Scoreboard\""}</th>
              <th key={19}>{"Game"}</th>
              <th key={20}>{"Course work"}</th>
            </tr>
          </thead>
          <tbody>
          {listOfItems}
          </tbody>
        </table>
      </div>
    )
  }
}

export default ListOfItems;