import React from 'react';
import './ListOfItems';
import ListOfItems from './ListOfItems';
import data from '../data.json';


let newData = data;
let table = null;


class SelectMentor extends React.Component{
  handleClick(e) {
    e.preventDefault();
    const currentMentor = document.getElementById("selectMentorID").value.toLowerCase();
    this.setState({
      currentMentor
    });
    newData = data.filter(item => item["interviewerGithub"]
      .toLowerCase() === currentMentor);
    table = newData.length === 0 ? 
      <div>
        <p className='massage-warning'>Sorry, can not find Mentor. </p>
        <p className='massage-warning'>Make sure you are follow the requirements</p>
        <p className='main-massage-warning'>https://github.com/yourGithub</p>
      </div>
      : <ListOfItems data={newData}/>;
  }
  render(){
    return (
      <div className="input__select-mentor">
        <label htmlFor="selectMentorID">Hello! Please introduce yourself with your github</label>
        <input id="selectMentorID" type="text" placeholder="https://github.com/"/>
        <button type="submit" onClick={this.handleClick.bind(this)}>submit</button>
        {table}
      </div>
    )
  }
}

export default SelectMentor;